import Endpoint from '@/services/Endpoint'

export default {
  beneficiaryTypes (payload) {
    return Endpoint().get('/s1/connect/beneficiary/beneficiaryType', {
      params: payload
    })
  },
  beneficiaryTypeTree (payload) {
    return Endpoint().get('/s1/connect/beneficiary/beneficiaryType/tree', {
      params: payload
    })
  },
  beneficiaries (payload) {
    return Endpoint().get('/s1/connect/beneficiary', {
      params: payload
    })
  },
  createBeneficiary (payload) {
    return Endpoint().post('/s1/connect/beneficiary', payload)
  },
  modifyBeneficiary (payload) {
    return Endpoint().patch(`/s1/connect/beneficiary/${payload.beneficiaryId}`, payload)
  },
  deleteBeneficiary (payload) {
    return Endpoint().delete(`/s1/connect/beneficiary/${payload.beneficiaryId}`, {
      params: payload
    })
  },
  beneficiaryWorkflowInstances (payload) {
    return Endpoint().get('/s1/connect/beneficiary/workflow/instance', {
      params: payload
    })
  }
}
